import React from 'react';
import './head.css';
import PaypalButton from '../paypal/paypal';

const Head = (props) => {

    const changetheme = () => {
        if (props.theme === "dark"){
            props.settheme("light");
        }else{
            props.settheme("dark");
        }
    }

    const changechat = () => {
        if (props.chat === "video"){
            props.setchat("");
        }else{
            props.setchat("video");
        }
    }

    return(<div className="d-flex justify-content-around w-100 h-100 mt-5 mb-5"> <button className="btn btn-secondary mt-3 mb-3" onClick={changetheme}>Changement de thÃ©me</button>
                                                                             <PaypalButton></PaypalButton>
                                                                             <button className="btn btn-secondary mt-3 mb-3" onClick={changechat}>chats</button> 
           </div>)
}

export default Head